.d-none {
  display: none;
}

.display-cover {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 87%;
  margin: 5% auto;
  position: relative;
}

video {
  width: 100%;
  background: rgba(0, 0, 0, 0.2);
  margin-bottom: 1.1rem;
}

.video-options {
  position: absolute;
  left: 1rem;
  top: 2rem;
}

.selfie, .original {
  width: 5.625rem;
  height: 9.375rem;
  border-radius: 4px;
  border: 2px solid whitesmoke;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.1);
  position: absolute;
  bottom: 1.5rem;
  background: white;
}

.original {
  left: 0.6rem;
}

.selfie {
  left: 7rem;
}

.controls {
  position: absolute;
  right: 1rem;
  top: 1rem;
  display: flex;
}
.controls > button {
  width: 45px;
  height: 45px;
  text-align: center;
  border-radius: 100%;
  margin: 0 6px;
  background: transparent;
}
.controlss > button:hover svg {
  color: white !important;
}
@media (min-width: 300px) and (max-width: 400px) {
  .controls {
    flex-direction: column;
  }
  .controls button {
    margin: 5px 0 !important;
  }
}
.controls > button > svg {
  height: 20px;
  width: 18px;
  text-align: center;
  margin: 0 auto;
  padding: 0;
}
.controls button:nth-child(1) {
  border: 2px solid red;
}
.controls button:nth-child(1) svg {
  color: red;
}
.controls button:nth-child(2) {
  border: 2px solid lightblue;
}
.controls button:nth-child(2) svg {
  color: lightblue;
}
.controls button:nth-child(3) {
  border: 2px solid lightgreen;
}
.controls button:nth-child(3) svg {
  color: lightgreen;
}
.controls button:nth-child(3):hover {
  background-color: green;
}
.controls > button {
  width: 45px;
  height: 45px;
  text-align: center;
  border-radius: 100%;
  margin: 0 6px;
  background: transparent;
}
.controls > button:hover svg {
  color: white;
}